body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
}

.stopwatch__center {
  justify-content: center;
  width: 5vmin;
  height: 5vmin;
  display: flex;
  position: relative;
}

.minutes-hand {
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation-name: rotate;
  animation-duration: 3600s;
  animation-timing-function: steps(60, start);
  animation-iteration-count: infinite;
  animation-delay: 60s;
  position: absolute;
  bottom: 50%;
}

.minutes-hand--speed-up {
  animation-duration: 600s;
  animation-delay: 10s;
}

.seconds-hand {
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation-name: rotate;
  animation-duration: 60s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  position: absolute;
  bottom: 50%;
}

.seconds-hand--speed-up {
  animation-duration: 10s;
}

@keyframes rotate {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

.center-point {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
}
